.page {
    margin: 20px 40px 50px;
    max-width: 1200px;
    align-self: center;

    @include ele(content) {
        display: flex;
        position: relative;
        display: flex;
        min-width: 1150px; }

    @include ele(half) {
        flex: 1 0;
        width: 700px;

        &:first-child {
            padding-right: 20px;
            border-right: $border; }

        &:last-child {
            padding-left: 20px; } }

    @include ele(half-header) {
        text-align: center;
        font-style: italic;
        margin: 0 0 20px; }

    @include ele(top) {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 40px 0; }

    @include ele(top-block) {
        display: flex;
        align-items: center; }

    @include ele(top-title) {
        margin: 0 0 0 10px; }

    @include wide() {

        @include ele(content) {
            min-width: 850px; }

        @include ele(half) {
            width: auto;
            min-width: 500px;

            &:first-child {
                padding-right: 10px; } } }

    @include medium() {
        margin: 20px auto 50px;
        width: 95%;

        @include ele(content) {
            width: $widthMedium;
            min-width: initial;
            margin: 25px auto 0; }

        @include ele(half) {

            &:first-child {
                border-right: 0; }

            &:last-child {
                display: none; } }

        @include ele(top) {
            margin: auto;
            width: $widthMedium;
            align-items: flex-start; } }

    @include small() {
        margin: 70px auto 50px;

        @include ele(content) {
            width: $widthSmall; }

        @include ele(half) {
            min-width: initial; }

        @include ele(top) {
            width: $widthSmall;
            flex-direction: column; }

        @include ele(top-section) {
            width: $widthSmall; }

        @include ele(top-title-block) {
            width: $widthSmall; } }

    @include tiny() {

        @include ele(content) {
            width: $widthTiny; }

        @include ele(top) {
            width: $widthTiny; } } }
